<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process24"></a>

<p><br></p>
<p><span class="rvts49">前言：</span>
<br><span class="rvts21">在《神届：原罪</span><span class="rvts20">2</span><span class="rvts21">》中，一共有</span><span class="rvts20">14</span><span class="rvts21">种职业。然而，从真正意义上讲，该游戏的职业特性并非特别突出，因为技能，学派，武器和装备对所有职业都没有限制，额外的属性加成也没有，因此职业只是一个比较</span><span class="rvts20"></span><span class="rvts21">灵活</span><span class="rvts20"></span><span class="rvts21">的词语。</span>
<br><span class="rvts21">而传统的</span><span class="rvts20">RPG</span><span class="rvts21">游戏大多数把职业给特定的分开，例如战士可以手持盾牌，但法师就不可以，骑士可以装备重甲，但牧师只能装备布甲之类，还有就是物理攻击类职业的主属性是力量，会获得力量分配点的额外物理伤害加成，然而在该游戏中这些都是不存在的。因此，玩家在建立一个角色的时候需要明白，职业在该游戏中只是一个模糊的概念，并非有特色划分的属性。就好比你可以是战士职业，但全部可以学所有法系技能。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">在起初，会被系统分配所谓的</span><span class="rvts20"></span><span class="rvts21">职业技能</span><span class="rvts20"></span><span class="rvts21">，而且根据目前版本来看，职业的概念只是刚开始建立角色时的技能分配不同而已，就连基础属性点和其它能力点数玩家都是可以自定义的。</span>
<br><span class="rvts21">换句通俗的话将，原罪</span><span class="rvts20">2</span><span class="rvts21">中的职业其实就是</span><span class="rvts20">4</span><span class="rvts21">大元素派（气，土，水，火）和</span><span class="rvts20">6</span><span class="rvts21">大战斗派（猎人，恶棍，死灵，召唤，变形，战争）相结合产生不同效果的玩法而已。</span>
<br><span class="rvts21">不过，如果真的按照系统给出的职业思路来游戏，那么笔者也总结出这些所谓</span><span class="rvts20"></span><span class="rvts21">职业</span><span class="rvts20"></span><span class="rvts21">的一些比较套路的概念。技能的具体效果请见</span><span class="rvts20">6~8</span><span class="rvts21">楼的</span><span class="rvts20"></span><span class="rvts21">学派技能篇</span><span class="rvts20"></span><span class="rvts21">。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



